<template>
  <div class="screen" @click="randomize">
    <p class="words">Please click/tap</p>
    <h1 class="content"><span v-text="character"></span> <span v-text="direction"></span> <span v-text="move"></span></h1>
    <a class="footer" href="http://tae8838.github.io" target="_blank">Made by Tae</a>
  </div>
</template>

<script>

  export default {
    data () {
      return {
        character: '',
        move: '',
        direction: '',
        characters: ['Mario', 'Luigi', 'Peach', 'Bowser',
          'Dr. Mario', 'Rosalina & Luma', 'Bowser Jr.',
          'Yoshi', 'Donkey Kong', 'Diddy Kong', 'Link',
          'Zelda', 'Sheik', 'Young Link', 'Ganondorf', 'Toon Link',
          'Samus', 'Zero Suit Samus', 'Kirby', 'Meta Knight',
          'King Dedede', 'Fox', 'Falco', 'Wolf', 'Pikachu',
          'Jigglypuff', 'Pichu', 'Mewtwo',
          'Squirtle', 'Ivysaur', 'Charizard',
          'Lucario', 'Greninja', 'Captain Falcon', 'Ness', 'Lucas',
          'Ice Climbers', 'Marth', 'Roy', 'Ike', 'Lucina',
          'Robin', 'Corrin', 'Mr. Game & Watch', 'Pit', 'Palutena',
          'Dark Pit', 'Wario', 'Olimar', 'R.O.B.', 'Villager',
          'Wii Fit Trainer', 'Little Mac', 'Shulk', 'Duck Hunt',
          'Snake', 'Sonic', 'Mega Man', 'Pac-Man', 'Ryu', 'Cloud',
          'Bayonetta', 'Mii Brawler', 'Mii Swordfighter',
          'Mii Gunner', 'Daisy', 'Piranha Plant', 'King K. Rool',
          'Ridley', 'Dark Samusε', 'Incineroar', 'Chrom',
          'Byleth', 'Isabelle', 'Inkling', 'Min Min', 'Ken',
          'Simon', 'Richter', 'Joker', 'Hero',
          'Banjo & Kazooie', 'Terry'],
        moves: ['Smash', 'Tilt', 'Air', 'Taunt'],
        directions: ['Down', 'Up', 'Forward', 'Back', 'Side']
      }
    },
    methods: {
      randomize() {
        this.character = this.chooseOne(this.characters)
        this.move = this.chooseOne(this.moves)
        this.direction = this.chooseOne(this.directions)

        if (['Taunt'].includes(this.move) && ['Forward', 'Back'].includes(this.direction)) {
          this.randomize()
        }

        if (['Smash', 'Tilt'].includes(this.move) && ['Back'].includes(this.direction)) {
          this.randomize()
        }

        if (['Air', 'Smash'].includes(this.move) && ['Side'].includes(this.direction)) {
          this.randomize()
        }
      },
      chooseOne(array) {
        return array[Math.floor(Math.random() * array.length)]
      }
    }
  }
</script>

